import {
  Component,
  OnInit,
  ViewChild,
  ElementRef,
  OnDestroy,
} from '@angular/core';

import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import { CustomTableConfig } from 'src/app/shared/templates/custom-table/models/config';
import { PNotifyService } from 'src/app/shared/services/core/pnotify.service';
import { PageChangedEvent } from 'ngx-bootstrap/pagination';
import { DetailPanelService } from 'src/app/shared/templates/detail-panel/detal-panel.service';
import { Subscription } from 'rxjs';
import { ControladoriaSaldosBancosService } from '../../services/saldos-bancos.service';

@Component({
  selector: 'controladoria-lanc-saldos-logs',
  templateUrl: './log.component.html',
  styleUrls: ['./log.component.scss']
})
export class ControladoriaSaldosBancosLogComponent implements OnInit, OnDestroy {
  @ViewChild('template', { static: false }) template: ElementRef;
  spinnerFullScreen: boolean = false;
  data: Array<any> = [];
  items: Array<any> = [];
  noResult = false;
  dadosEmpty = false;
  log: any = {};
  $saldosBancosSubscription: Subscription;
  $saldosBancosServiceSubscription: Subscription;

  appTitle = 'Detalhes';

  modalRef: BsModalRef;
  config = {
    animated: false,
    ignoreBackdropClick: true,
    class: 'modal-xxl',
  };

  /* Painel de detalhes */
  $showDetailPanelSubscription: Subscription;
  showDetailPanel = false;
  detailPanel = undefined;

  tableLogConfig: Partial<CustomTableConfig> = {
    hover: true,
  };

  /* Paginação */
  itemsPerPage: number = 10;
  totalItems: number;
  currentPage: number = 1;
  begin: number = 0;
  end: number = 10;

  constructor(
    private modalService: BsModalService,
    private saldosBancosService: ControladoriaSaldosBancosService,
    private notice: PNotifyService,
    private detailPanelService: DetailPanelService
  ) {}

  ngOnInit(): void {
    this.openModal();
    this.onDetailPanel();
  }

  ngOnDestroy() {
    this.$saldosBancosSubscription.unsubscribe();
    this.$saldosBancosServiceSubscription.unsubscribe();
  }

  openModal(): void {
    this.$saldosBancosServiceSubscription = this.saldosBancosService
      .getStateModal()
      .subscribe((response) => {
        this.data = response;
        this.modalRef = this.modalService.show(this.template, this.config);
        this.showDetailPanel = false;
        this.getLog();
      });
  }

  getLog() {
    this.items = [];
    this.spinnerFullScreen = true;
    let ID_BANC_SALD = this.data['ID_BANC_SALD'];

    this.saldosBancosService.getLog(ID_BANC_SALD).subscribe((response) => {
      if (response.status === 204) {
        this.notice.notice('Não existe log registrado.');
        this.spinnerFullScreen = false;
      } else {
        this.items = response.body['data'];
        this.totalItems = response.body['data'].length;
        this.dadosEmpty = false;
        this.noResult = true;
        this.spinnerFullScreen = false;
      }
    });
  }

  /* Paginação */
  onPageChanged(event: PageChangedEvent): void {
    this.begin = (event.page - 1) * event.itemsPerPage;
    this.end = event.page * event.itemsPerPage;
  }
  /* Paginação */

  onDetailPanel(): void {
    this.$showDetailPanelSubscription = this.detailPanelService.config.subscribe(
      (event: any) => {
        if (this.detailPanel !== 'modal') return;

        this.showDetailPanel = event.showing;
        if (!this.showDetailPanel)
          this.items.map((item) => (item.selected = false));
      }
    );

    this.$saldosBancosSubscription = this.saldosBancosService
      .getDetailPanel()
      .subscribe((event) => {
        this.detailPanel = event;
        this.detailPanelService.loadedFinished(false);
      });
  }

  openTab(log) {
    this.items.map((item) => (item.selected = false));
    this.log = log;
    this.log.selected = true;
  }

  clickEvent(item) {
    this.saldosBancosService.setDetailPanel('modal');
    this.items.forEach((element) => {
      if (element.INDICE != item.INDICE) {
        element.STATUS = false;
      } else if (item.STATUS == true) {
        item.STATUS = true;
      } else {
        item.STATUS = !item.STATUS;
      }
    });
  }
}
